@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap');

.primary_button{
    font-size: 18px;
    padding: 3px 15px;   
    background-color: rgb(0,128,128);
    border: solid 4px teal;
    color: white;
    opacity: .7;
    border-radius: 15px;
    cursor: pointer;
}
.primary_button:hover{
    opacity: 1;
}
@media only screen and (max-width: 600px) {
    .primary_button{
        font-size: 10px;
        opacity: 1;
        padding: 5px;   
    }

}   
hr {
    width: 100%;
}
.App{
    font-family: 'Poppins', sans-serif;
}
a{
    color: #fff;
    text-decoration: none;
    letter-spacing: 0.15em;
    font-size: .9em;
}
.resume_button{
    background: transparent;
    border: none;
    position: fixed;
    top: 40px ;
    right: 40px;
    transform: scale(2);
    opacity: .4;
    cursor: pointer;
}
.resume_button:hover{
    color: rgb(0,128,128);
    opacity: 1
}